body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

form {
    margin: 20px auto;
}
button {
    background-color: #f45f04;
    padding: 5px 10px;
    border: 1px solid #9e3e04;
    outline:  none;
    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px;
}
button:hover {
    cursor:pointer;
    box-shadow: 0 0 10px 0px #9e3e04;
}
button[type="button"] {
    border-radius: 10px;
    margin-top: 10px;
}
input {
    padding: 5px 10px;
    border:1px solid #9e3e04;
    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px;
    outline: none;
}
input:focus {
    box-shadow: 0 0 10px 0px #9e3e04;
    
}
* {
    box-sizing: border-box;
}
.App {
  text-align: center;
}

.App-link {
  color: #61dafb;
}

.weather {
    width: 100%;
    display: -webkit-flex;
    display: flex;
    margin: 0 auto;
    -webkit-justify-content: center;
            justify-content: center;
    border-style: solid;
    border-width: 1px;
    border-color: transparent #f45f04 #f45f04 #f45f04; 
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
    background-color: white;
    box-shadow: 0 0 10px 0px #9e3e04;
    padding: 20px 5px;
    font-size: 14px;
    position: relative;   
}
.weather div {
    width: 20%;
    text-align: center;
    position: relative;
    padding: 2px;
}
.weather div span{
    display: block;
}
.weather div:not(:last-of-type)::after {
    content: "";
    position: absolute;
    height: 60%;
    width: 1px;
    box-shadow: 0 0 10px 0px #9e3e04;
    background-color: #f45f04;
    right: 0;
    top: 50%;
    -webkit-transform: translate(50%, -50%);
            transform: translate(50%, -50%);
    z-index: 10;
}
@media (max-width: 800px) {
    .weather {
        width: 75%;
        -webkit-flex-direction: column;
                flex-direction: column;
        border-color:  #f45f04 #f45f04 #f45f04 transparent; 
        border-bottom-left-radius: 0;
        border-top-right-radius: 10px;
        border-bottom-right-radius: 10px;
        padding: 5px 20px;
    }
    .weather div {
        display: -webkit-flex;
        display: flex;
        -webkit-justify-content: center;
                justify-content: center;
        -webkit-align-items: center;
                align-items: center;
        width: 100%;
        position: relative;
        height: 20%;
    }
    .weather div:not(:last-of-type)::after {
        content: "";
        position: absolute;
        width:  60%;
        height: 1px;
        box-shadow: 0 0 10px 0px #9e3e04;
        background-color: #f45f04;
        top:  100%;
        left: 50%;
        -webkit-transform: translateX(-50%);
                transform: translateX(-50%);
        z-index: 10;
    }
    .weather div p, .weather div span {
        padding: 0 10px;
    }
}
@media (max-width: 600px) {
    .weather {
        font-size: 12px;
        -webkit-flex-direction: column;
                flex-direction: column;
  
    }
}
.content {
    max-width: 960px;
    margin: 0 auto;
}
.info {
    width: 100%;
    height: 100%;
}
.info > div  {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}
h2 {
    text-align: left;
    color: #9e3e04;
}
.tabs {
    list-style-type: none;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    padding: 0;
    margin: 20px 0 0;
    margin-bottom: -1px;
}
.tabs li {
    display: inline-block;
    width: 20%;
    padding: 20px;
    border: 1px solid #f45f04;
    border-top-left-radius: 10px; 
    border-top-right-radius: 10px;
    box-shadow: 0 0 10px 0px #9e3e04;
    background-color: transparent;
}
.tabs li div {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}
.tabs li span p {
    margin: 0;
}
.tabs li span p:last-child {
    color: #9e3e04;
    font-weight: 700;
    font-size: 16px;
}
.tabs li.active {
    border-color: #f45f04 #f45f04 transparent #f45f04;
    position: relative;
    top: -5px;
    background-color: white;
    z-index: 2;
    -webkit-transform: scaleY(1.05);
            transform: scaleY(1.05);
}
.tabs li.active div {
    width: calc(100% - 1px);
    position: absolute;
    left: 0;
    padding: 0 20px;
    height: 85px;
    background-color: white; 
}
.tabs li.active > p {
    background-color: #f45f04;
    border-radius: 10px;
    border: 1px solid #9e3e04;
}
.preloader {
    height:100%;
    width:100%;
    background:rgba(0, 0, 0, 0.5);
    position:fixed;
    left:0;
    top:0;
    display: -webkit-flex;
    display: flex;
    z-index: 10;
}
.spinner{
    width: 80px;
    height: 80px; 
    border: 2px solid #f3f3f3;
    border-top:3px solid #f25a41;
    border-radius: 100%;   
    position: absolute;
    top:0;
    bottom:0;
    left:0;
    right: 0;
    margin: auto;  
    -webkit-animation: spin 1s infinite linear;  
            animation: spin 1s infinite linear;
}
.error {
    display: -webkit-flex;
    display: flex;
    width: 100%;
    height: 150px;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    font-size: 50px;
    color:#9e3e04;
}

@-webkit-keyframes spin {
    from{
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }to{
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes spin {
    from{
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }to{
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}
@media (max-width: 800px) {
    .content {
        width: 96%;
    }
    .info > div {
        -webkit-flex-direction: row;
                flex-direction: row;
        max-height: 100vh;
    }
    .tabs {
        -webkit-flex-direction: column;
                flex-direction: column;
        width: 25%;
        max-height: 100%;
        margin: 0 0 0 10px;
    }
    .tabs li {
        display: -webkit-flex;
        display: flex;
        width: 100%;
        height: calc(100% / 6);
        padding: 0 20px;
        border: 1px solid #f45f04;
        border-bottom-left-radius: 10px;
        border-top-right-radius: 0; 
        box-shadow: 0 0 10px 0px #9e3e04;
    }
    .tabs li div {
        -webkit-flex-direction: column;
                flex-direction: column;
        margin-left: 10px;
        padding: 10px 0;

    }
    .tabs li span p {
        display: inline-block;
    }
    .tabs li > p {
        padding: 2px;
        width: 5ch;
        text-align: center;
    }
    .tabs li.active {
        border-color: #f45f04 transparent #f45f04 #f45f04;
        left: -4px;
        top:0;
        z-index: 2;
        -webkit-transform: scaleX(1.05);
                transform: scaleX(1.05);
    }
    .tabs li.active div {
        height: calc(100% - 1px);
        position: absolute;
        top: 0;
        left: 45%;
        padding: 10px 0;       
        margin: 0;
        width: 110px;
        background-color: white; 
    }

}
@media (max-width: 630px) {
    .tabs li div {
        -webkit-justify-content: space-around;
                justify-content: space-around;
    }
    .tabs li.active div {
        width: 90px;
        left: 55%;
    }
    .tabs li {
        font-size: 12px;
    }
}
@media (max-width: 500px) {
    .tabs li {
        padding: 0 5px;
    }
    .tabs li span, .tabs li p {
        font-size: 10px;

    }
    .tabs li.active div {
        -webkit-flex-direction: column;
                flex-direction: column;
        margin-left: 10px;
        padding: 10px 0;
        width: 70px;
        left:35%;
     }
    }

